.h {
  text-align: center; /* Center the content horizontally */
  color: black;
  background-image: url("../img/about/sfondoNero.png");
  background-position: top center;
  background-size: 100% 120%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-color: rgba(0, 0, 0, 0.4);
}

h1 {
  margin-bottom: 40px;
}

.title {
  padding: 20px;
  color: white;
  height: 80vh;
}

.title img {
  height: 50%;
  border-radius: 50%;
}

.portfolioLink {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 40px;
}
.portfolioLink p {
  padding-right: 5px;
}
.portfolioLink a {
  text-decoration: none; /* Remove the default underline */
  color: white; /* Set the text color to green */
  padding: 10px 10px;
  border-radius: 20px;
  background-color: #2a620a; /* Set the initial background to transparent */
  transition: background-color 0.3s, color 0.3s;
}
.portfolioLink a:hover {
  background-color: #73a848; /* Set the background color to light green on hover */
  color: black;
}

@media screen and (max-width: 600px) {
  .h {
    width: 100%;
    background-size: cover; /* Adjust background size for small screens */
  }
  .h h1 {
    font-size: 20px;
  }
  .h p {
    font-size: 15px;
  }
  .h a {
    font-size: 12px;
  }
}
