/* projectDetail.css */
.p {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url("../img/sfondo.jpg");
  background-position: top center;
  background-size: 100%;
  background-repeat: repeat;
  text-align: center;
  margin-top: 30px;
  padding: 0 50px;
}
.p-desc {
  padding: 20px;
}
.p-desc ul {
  list-style: none;
}

.p-desc p {
  padding: 0px 100px 20px 100px;
  text-align: justify;
}
.p-desc a {
  background-color: #2a620a; /* Initial color */
  color: white;
  border: none;
  margin: 30px;
  padding: 10px 10px;
  border-radius: 20px;
  font-size: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  text-decoration: none;
}
.p-desc a:hover {
  background-color: #73a848;
  color: black;
}
.p-desc-info {
  padding-bottom: 30px;
}
.p-desc-details {
  padding-bottom: 20px;
}

.p-imageSingle {
  width: 80%;
}
.p-image2row,
.p-image3row {
  display: flex;
  flex-wrap: wrap;
}
.p-image2row-items img,
.p-image3row-items img {
  width: 70%;
  height: auto;
}
.p-image2row-items {
  width: calc(50% - 10px); /* Two items per line, adjust width as needed */
  box-sizing: border-box;
  padding: 20px;
  margin-bottom: 10px;
}

.p-image3row-items {
  width: calc(33.33% - 10px); /* Two items per line, adjust width as needed */
  box-sizing: border-box;
  padding: 20px;
  margin-bottom: 10px;
}

.p-imageSingle-item img {
  width: 70%;
  height: auto;
}
@media screen and (max-width: 600px) {
  .p {
    width: 100%;
    background-size: cover;
    padding: 0;
  }
  .p-desc p {
    font-size: 15px;
    padding: 5px 20px;
  }
  .p-desc img {
    width: 80%;
  }
  .p-desc-links {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .p-image2row-items,
  .p-image3row-items {
    width: 100%;
    padding: 0;
  }
}
